import axios from 'axios';
import React, { Component } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

import Loading from '../components/Loading';

const Image = styled.img`
    width: 8em;
    height: 10em;

`;
const Div = styled.div`
    height: 100vh;

`;


class PokemonDetails extends Component {
    state ={
        imageUrl: '',
        name: '',
        pokemonIndex: '',
        stats: {
            hp: '',
            attack: '',
            defense: '',
            speed: '',
            specialAttack: '',
            specialDefense: ''
        },
        error: '',
        loading: true,
    };

    async componentDidMount () {
        try{
        const { pokemonIndex } =this.props.match.params;

        const pokemonUrl =`https://pokeapi.co/api/v2/pokemon/${pokemonIndex}/`;

        const pokemonRes = await axios.get(pokemonUrl);

        const name = pokemonRes.data.name;

    
        const imageUrl = pokemonRes.data.sprites.front_default;

        let {hp, attack, defense, speed, specialAttack, specialDefense } = '';

        pokemonRes.data.stats.map(stat =>{
            switch (stat.stat.name) {
                case 'hp':
                hp = stat['base_stat'];
                break;
                case 'attack':
                attack = stat['base_stat'];
                break;
                case 'defense':
                defense = stat['base_stat'];
                break;
                case 'speed':
                speed = stat['base_stat'];
                break;
                case 'special-attack':
                specialAttack = stat['base_stat'];
                break;
                case 'special-defense':
                specialDefense = stat['base_stat'];
                break;
            }

        });

        this.setState({
            stats: {
                hp,
                attack,
                defense,
                speed,
                specialAttack,
                specialDefense
            },
            name,
            imageUrl,
            loading: false,
           
           
        });

         console.log (pokemonRes.data);   
      
     } catch(error){
        console.log(error.message)};
     
        //alert ("There is an error to load the data");
        let error = new Error("There is an error to load the data!");
        this.setState({error});
      
  }


    render() {
        return(
           <Div className="container">
            {this.state.loading && (
                 <Loading />
            )}
            {this.state.loading && this.state.error && (
                <div className="text-center text-danger mt-5">
                    {/* <p className="text-center lead">{this.state.error}</p> */}
                    <h3 className="lead text-center mt-3 font-weight-bold text-danger">There is an error to load the data!</h3>
                    <Link to="/" className="btn btn-danger mt-5">Go Back</Link>
                </div>
            )}
           
            {!this.state.loading && this.state.stats && (
            <div className="text-center mx-auto">
                <Image className="card-img-top rounded mx-auto img-fluid" src={this.state.imageUrl} alt="pokemon1" />
                <h3 className="text-capitalize col-9 bg-danger text-white mx-auto mb-0">{this.state.name}</h3>
                <table className="table table-striped bg-light col-9 mx-auto mb-0">
                    <thead>
                        <tr>
                            <th scope="col">#</th>
                            <th scope="col" className="h5">Stats</th>
                            <th scope="col" className="h5">Value</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <th scope="row">1</th>
                            <td>HP</td>
                            <td>{this.state.stats.hp}</td>
                        </tr>
                        <tr>
                            <th scope="row">2</th>
                            <td>ATTACK</td>
                            <td>{this.state.stats.attack}</td>
                        </tr>
                        <tr>
                            <th scope="row">3</th>
                            <td>DEFENSE</td>
                            <td>{this.state.stats.defense}</td>
                        </tr>
                        <tr>
                            <th scope="row">4</th>
                            <td>SPEED</td>
                            <td>{this.state.stats.speed}</td>
                        </tr>
                        <tr>
                            <th scope="row">5</th>
                            <td>SPECIAL-ATTACK</td>
                            <td>{this.state.stats.specialAttack}</td>
                        </tr>
                        <tr>
                            <th scope="row">6</th>
                            <td>SPECIAL-DEFENSE</td>
                            <td>{this.state.stats.specialDefense}</td>
                        </tr>
           
                    </tbody>
                </table>
                <div className="text-left col-9 bg-dark text-white mx-auto mt-0 pb-2 pt-2">
                            <span> Data From </span>
                            <a href="https://pokeapi.co/" target="_blank" className="card-link">
                            PokeAPI.co
                            </a>
                          
                </div>
                <Link to="/" className="btn btn-danger mt-4 mb-5">Go Back</Link>

            </div>         
        
            )}
            </Div>
        );
    }
}
    
export default PokemonDetails;

