import React, { useState, useEffect } from 'react';
import loader from '../assets/loader.gif';

const Loading = () => {
    const [ spinner, setSpinner ] = useState(true);

    useEffect(() => {
        window.setTimeout(() => setSpinner(false), 1500)

    }, []);

    return ( 
        <div>
        {spinner && (
        <div >
            <img className="text-center mx-auto mt-5" src={loader} alt="spinner" style={{width:'12em', height: '12em'}} />

        </div>
        )}
        </div>
    )
}





export default Loading;