import axios from 'axios';
import React, { Component } from 'react';


import Loading from '../components/Loading'
import PokemonCard from '../components/PokemonCard';

class PokemonList extends Component {
    state = {
        url: 'https://pokeapi.co/api/v2/pokemon?limit=151',
        pokemon: null,
        error: '',
        loading: true,
        
    }

    async componentDidMount() {
        try{
        const res = await axios.get(this.state.url);
        this.setState({ pokemon: res.data['results']});
        this.setState({loading: false});
        
        }catch(error){
            console.log(error.message);
            this.setState({error});
        }

    }

    render () {

        return (
            <div className="m-md-5">
                <div className="text-center m-5">
                    <h1>Welcome to Fei's Poke API</h1>
                    <p className="h5">You will find the top 151 pokemon here! And just one click away to see your favorite pokemon's stats! </p>
                </div>
                <div className="ml-5 mr-5 pl-5 pr-5">
                    {this.state.loading &&  (
                        <Loading />
                    )}
                    {this.state.loading  && this.state.error && (
                        <h3 className="lead text-center mt-3 font-weight-bold text-danger">Error was found to load data.</h3>
                    )}
                    {!this.state.loading  && !this.state.error && this.state.pokemon && (
                    <div className="row">
                    {this.state.pokemon.map((pokemon, i) => (
                        <PokemonCard
                            name={pokemon.name}
                            url={pokemon.url}
                            key={pokemon.name}
                            id={i +1}
                            />
                    ))}

                    </div>
                    )}
                </div>  
           </div> 
        );
    }
}

export default PokemonList;